.card-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  min-width: 265px;
}

.country-card {
  background-color: white;
  width: 265px;
  height: 340px;
  margin-bottom: 70px;
  border-radius: 6px;
  overflow: hidden;
  -webkit-box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
          box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
}

.country-card .flag-image {
  width: 265px;
  height: 160px;
  cursor: pointer;
}

.country-card .country-name {
  margin: 20px 20px 12px 20px;
  cursor: pointer;
}

.country-card .country-details {
  margin-left: 20px;
}

.country-card .country-details .population, .country-card .country-details .region, .country-card .country-details .capital {
  font-size: .8rem;
}

.country-card .country-details .population b, .country-card .country-details .region b, .country-card .country-details .capital b {
  font-size: .8rem;
}
