@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap");
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: "Nunito Sans", sans-serif;
  font-size: 14px;
  color: #111517;
}

ul[role="list"],
ol[role="list"] {
  list-style: none;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #fafafa;
}

body a {
  text-decoration: none;
}

#root {
  width: 100%;
}

img,
picture {
  max-width: 100%;
  display: block;
}

.container {
  margin-left: 200px;
  margin-right: 200px;
}

.top-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
  padding: 0 200px;
  height: 80px;
  -webkit-box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
          box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
}

.top-header .title {
  display: inline-block;
  padding-right: 12px;
}

.top-header .title a {
  color: #111517;
  font-size: 1.2rem;
  font-weight: 800;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.top-header .mode {
  display: inline-block;
  font-weight: 600;
  cursor: pointer;
}

.theme-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.theme-container .fa-moon {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 4px;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    -webkit-transition-duration: 0.01ms !important;
            transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

@media (max-width: 675px) {
  .container {
    margin-left: 10px;
    margin-right: 0;
  }
  .top-header {
    padding: 0 10px;
  }
  .search-box {
    width: 315px;
  }
  .search-header {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .card-grid {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-top: 25px;
  }
  .top-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .sc-container-country {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .sc-container-country .sc-container-details {
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 20px;
  }
  .sc-flag-container {
    max-width: 340px;
    height: auto;
  }
  .sc-flag-container .sc-flag {
    width: 340px;
  }
}
